* {
  margin: 0;
  size: 0;
  padding: 0;
  box-sizing: border-box;
  vertical-align: top;
}

body {
  overflow-x: hidden;
}

.popper {
  font-family: announcement;
  position: absolute;
  width: 100%;
  text-align: center;
  display: block;
  top: 20%;
  color: rgb(255, 255, 255);
  backface-visibility: hidden;
  font-size: 100px;
  animation-name: popgoesthediv;
  animation-duration: 5s;
  animation-fill-mode: backwards;
  opacity: 0;
}

@keyframes popgoesthediv {
  0% {
    opacity: 0;
    transform: translateX(-1000px);
  }

  30% {
    opacity: 1;
    transform: translateX(0);
  }
  70% {
    opacity: 1;
    transform: translateX(0);
  }

  100% {
    opacity: 0;
    transform: translateX(1000px);
  }
}

.Header {
  height: 100vh;
  background-image: url("./final/69.jpg");
  background-attachment: fixed;
  background-size: cover;
  background-position: top;
  position: relative;

  // clip-path: polygon(0 0, 100% 0, 100% 75vh, 0 100%);
  .text-box {
    position: absolute;
    top: 70%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
    .heading-primary {
      color: #fff;
      // text-transform: uppercase;

      backface-visibility: hidden;
      margin-bottom: 50px;
      .heading-primary-main {
        display: block;
        font-family: "myFirstFont";
        font-size: 80px;
        font-weight: 400;
        letter-spacing: 10px;

        animation-name: moveInLeft;
        animation-duration: 2s;
      }
      .heading-primary-sub {
        display: block;
        font-size: 20px;
        font-weight: 700;
        letter-spacing: 17.4px;

        font-family: announcement;
        animation-name: moveInRight;
        animation-duration: 2s;
        animation-timing-function: ease-out;
      }
    }
  }
}

