@font-face {
  font-family: text_font;
  // src: url("./Static/AlexBrush-Regular.ttf");
  // src: url("./Static/LoversQuarrel-Regular.ttf");
  src: url("./Static/EuphoriaScript-Regular.ttf");
}

.text {
  position: relative;
  .announcement {
    z-index: 100;
    position: absolute;
    background-color: white;
    left: 50%;
    transform: translate(-50%, -50%);
    padding: 25px 0;
    width: 400px;
    text-align: center;
  }
  .border {
    font-family: text_font;
    font-size: 28px;
    span {
      font-size: 46px;
      vertical-align: bottom;
      text-decoration: none;
    }
    position: relative;
    margin: 0 100px;
    padding: 200px;
    .corner {
      // background-color: red;
      position: absolute;
      width: 100px;
      height: 110px;
      z-index: 100;
      &::before {
        width: 100px;
        height: 110px;
        display: inline-block;
        background-size: cover;
        background-position: top;
        background-image: url("./final/corner_flowers.jpg");
      }
      &._1 {
        bottom: 0px;
        right: -50px;
        &::before {
          content: "";
          transform: rotate(270deg);
        }
      }

      &._2 {
        &::before {
          content: "";
          transform: rotate(180deg);
        }
        top: 0px;
        right: -50px;
      }

      &._3 {
        &::before {
          content: "";
          transform: rotate(90deg);
        }
        top: 0px;
        left: -50px;
      }

      &._4 {
        &::before {
          content: "";
        }
        bottom: 0px;
        left: -50px;
      }
    }
  }
  .text_1 {
    z-index: 200;
    top: 10%;
    left: 5%;
    position: absolute;
  }
  .text_2 {
    .span {
      position: absolute;
      display: inline;
      transform: translate(-100%, -40%);
      font-size: 4em;
    }
    z-index: 200;
    position: absolute;
    top: 25%;
    left: 10%;
    width: 80%;
    // text-align: center;
  }
  .text_3 {
    font-size: 1.5em;
    z-index: 200;
    position: absolute;
    left: 70%;
    top: 75%;
  }
  background-color: rgb(255, 255, 255);
  padding: 50px 0;
}

@media only screen and (max-width: 800px) {
  .pic {
    display: block;
    position: static;
    width: 100vw;
    margin: none;
    padding: none;
    border: none;
    height: 100vh;
  }
}